@font-face {
  font-weight: normal;
  font-family: "PT Sans";
  font-style: normal;
  src: url("../fonts/ptsans.woff") format("woff"), url("../fonts/ptsans.woff2") format("woff2");
}
@font-face {
  font-weight: bold;
  font-family: "PT Sans";
  font-style: normal;
  src: url("../fonts/ptsansbold.woff") format("woff"), url("../fonts/ptsansbold.woff2") format("woff2");
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: normal;
  font-style: normal;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 1200px) {
  .main-wrapper {
    width: 1200px;
    margin: 0 auto;
    background-color: #f2f2f2;
    box-shadow: 0 0 15px 5px rgba(0, 0, 1, 0.2);
  }
}
.header {
  width: 320px;
  margin: 0 auto;
  min-height: 667px;
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #e5f2fa;
  background-image: url("../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center 280px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .header {
    background-image: url("../img/bg-back-mobile@2x.jpg");
  }
}
.header::before {
  content: "";
  position: absolute;
  top: 641px;
  left: 50%;
  margin-left: -160px;
  width: 320px;
  height: 26px;
  background-image: url("../img/bg-intro-triangle-mobile.svg");
  background-repeat: no-repeat;
  background-size: 102%;
  background-position: -3px 0;
}
.header::after {
  content: "";
  position: absolute;
  top: 369px;
  left: 50%;
  margin-left: -160px;
  width: 460px;
  height: 350px;
  background-image: url("../img/text-sedona-index.svg");
  background-repeat: no-repeat;
  background-size: 320px 215px;
}
@media (min-width: 768px) {
  .header {
    width: 768px;
    min-height: 574px;
    background-image: url("../img/bg-back-tablet@1x.jpg");
    background-position: center 56px;
  }
  .header::before {
    top: 518px;
    margin-left: -384px;
    width: 768px;
    height: 57px;
    background-image: url("../img/bg-intro-triangle-tablet.svg");
  }
  .header::after {
