.main-nav {
  position: relative;
  width: 320px;
  margin: 0 auto;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .main-nav {
    width: 768px;
    background-color: #fff;
  }
}
@media (min-width: 1200px) {
  .main-nav {
    width: 1200px;
  }
}
@media (max-width: 767px) {
  .main-nav__wrapper::after {
    content: "";
    display: block;
    background-color: #fff;
    height: 56px;
  }
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    width: 768px;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .main-nav__wrapper {
    width: 1200px;
  }
}
@media (max-width: 767px) {
  .main-nav__toggle {
    position: absolute;
    top: 240px;
    right: 20px;
    width: 23px;
    height: 21px;
    background-color: transparent;
    cursor: pointer;
    border: none;
  }
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
/*кнопка-гамбургер*/
@media (max-width: 767px) {
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 23px;
    height: 4px;
    background-color: #81b3d2;
    box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2;
  }
}
/*кнопка-крестик*/
@media (max-width: 767px) {
  .main-nav--opened .main-nav__toggle {
    top: 16px;
    right: 20px;
  }
  .main-nav--opened .main-nav__toggle::before,
  .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 10px;
    right: -3px;
    width: 28px;
    height: 4px;
    background-color: #fff;
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(-45deg);
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(45deg);
  }
}
/*при выключенных скриптах кнопка-крестик не должна отображаться*/
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.list-nav {
  display: flex;
