  .list-nav__item {
    border: none;
    width: 20%;
  }
  .list-nav__item--margin {
    margin-left: 20%;
  }
}
.main-nav--nojs .list-nav__item {
  display: block;
}
.list-nav__item a {
  display: block;
  padding-top: 8px;
  padding-bottom: 6px;
  text-decoration: none;
  text-align: center;
  color: #fff;
}
.list-nav__item a:hover,
.list-nav__item a:focus {
  background-color: #669ec0;
}
.list-nav__item a:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .list-nav__item a {
    padding-top: 15px;
    padding-bottom: 15px;
    color: #000;
  }
  .list-nav__item a:hover,
  .list-nav__item a:focus {
    background-color: #fff;
    color: #81b3d2;
  }
  .list-nav__item a:active {
    color: rgba(0, 0, 0, 0.3);
  }
}
@media (min-width: 768px) {
  .list-nav__item--current a {
    color: #766357;
  }
  .list-nav__item--current a:hover,
  .list-nav__item--current a:focus {
    color: #604e43;
  }
  .list-nav__item--current a:active {
    color: rgba(118, 99, 87, 0.3);
  }
}
